<template>
  <table>
    <tr class="row">
      <td class="col">type</td>
      <td class="col">height</td>
      <td class="col">actions</td>
    </tr>

    <tr v-for="layer of layers" :key="layer.id">
      <td>
        <select
          name="typeImage"
          @change="$parent.updateLayerType($event, layer.id)"
        >
          <option
            v-for="type of layerTypes"
            :selected="type.type == layer.type"
            :key="layerTypes.indexOf(type)"
            >{{ type.type }}</option
          >
        </select>
      </td>

      <td>
        <input
          type="number"
          min="1"
          name="layerHeight"
          :value="layer.height"
          @input="$parent.updateLayerHeight($event, layer.id)"
        />
      </td>

      <td>
        <button @click="$parent.deleteLayer(layer.id)">
          delete
        </button>
      </td>
    </tr>
  </table>
</template>

<script>
export default {
  name: "Table",
  props: {
    layers: Object,
    layerTypes: Object,
  },
};
</script>
