import db from './db';

let api = {
    addLayer() {
        let newLayer = db.layerTypes[0]
        newLayer.id = db.layers.length + 1
        this.layers.push(newLayer);
        this.updateSum();
    },
    deleteLayer(id) {
        this.layers = this.layers.filter(layer => layer.id != id);
        this.updateSum();
    },
    updateLayerHeight(event, id) {
        let layerToUpdate = this.layers.find(layer => layer.id == id)
        layerToUpdate.height = event.target.value;
        this.updateSum();
    },
    updateLayerType(event, id) {
        console.log(id);
        let newTypeTitle = event.target.value.toString()
        let layerToUpdate = this.layers.find(layer => layer.id == id)
        let type = this.layerTypes.find((type) => type.type == newTypeTitle);
        console.log(type);
        layerToUpdate.type = newTypeTitle;
        layerToUpdate.pricePerLayer = type.pricePerLayer;
        console.log(this.layers);
    },
    updateSum() {
        this.sum = this.layers
            .map((item) => item.pricePerLayer * item.height)
            .reduce((a, b) => a + b);
    }
}
export default api